<template>
  <div>
    <!-- container -->
    <div class="container__base">
      <Menubar></Menubar>
      <!--  -->
      <div class="visual">
        <div class="visual__head">
          <div class="visual__txt">
            <transition></transition>
            <div class="show"></div>
            <div class="show--two">AI </div>
            <div class="show--thr"></div>
            <div class="show--fou"><br class="mobile_break"></div>
          </div>
        </div>
        <div class="visual__btn"><router-link to="/solution">
            <Buttonbase></Buttonbase>
          </router-link></div>
        <div class="visual__animate">
          <div class="animate"><img class="visual__arrow upthr" src="../assets/img/visual_arrow.png" alt=""></div>
          <div class="animate"><img class="visual__arrow uptwo" src="../assets/img/visual_arrow.png" alt=""></div>
          <div class="animate"><img class="visual__arrow up" src="../assets/img/visual_arrow.png" alt=""></div>
        </div>
      </div>
      <!--  -->
      <div class="direction">
        <h3 class="direction__title"></h3>
        <h1 class="direction__caption"> AI </h1>
        <div class="direction__card">
          <span class="material-icons-outlined">flash_on</span>
          <h3></h3>
          <div></div>
        </div>
        <div class="direction__card">
          <span class="material-icons-sharp">auto_awesome</span>
          <h3></h3>
          <div></div>
        </div>
        <div class="direction__card">
          <span class="material-icons-round">library_add</span>
          <h3></h3>
          <div> AI </div>
        </div>
      </div>
      <!--  -->
      <div class="partnerblock">
        <div>
          <h3 class="partner__title"></h3>
          <div class="partner__iconblock">
            <div class="partner__icon"><img :src="require(`../assets/img/logo_one_${item}.png`)" alt=""
                v-for="item in partnerone"></div>
            <div class="partner__icon"><img :src="require(`../assets/img/logo_two_${item}.png`)" alt=""
                v-for="item in partnertwo"></div>
          </div>
          <Partner></Partner>
        </div>
        <div>

        </div>
      </div>
    </div>
    <!-- container -->
    <div class="toolblock">
      <div class="container__base">
        <div class="tool__title"><br class="mobile_break"> AI</div>
        <div class="tool__search"><span class="material-icons">search</span><input type="text" placeholder="">
        </div>
        <div class="tool__card">
          <Filtertool />
        </div>
        <div class="tool__cntnt">
          <Card></Card>
        </div>
      </div>
    </div>
    <!-- container -->
    <div class="container__base">
      <div class="goto">
        <div class="goto__sign"></div>
        <router-link to="/solution">
          <Buttonbase>
            <div></div><span class="material-icons">arrow_outward</span>
          </Buttonbase>
        </router-link>
      </div>
      <div class="map">
        <div class="map__sign font-bold"><br></div>
        <!-- <div><img src="../assets/img/ai_logo.png" alt="" class="map__logo"></div> -->
      </div>
      <Footer></Footer>
    </div>
  </div>
</template>

<script>
import Partner from './Partner.vue';
import Card from './Card.vue';
import Footer from './Footer.vue';
import Filtertool from './Filtertool.vue';
import Menubar from './Menubar.vue';
import Buttonbase from './Button.vue';
import $ from "jquery";

window.$ = $;

export default {
  name: 'HelloWorld',
  data() {
    return {
      partnerone: [1, 2, 3, 4, 6, 7, 5],
      partnertwo: [1, 2, 3, 4, 5, 6, 1],
      show:false,
      showtwo:false,
      showthr:false,
      showfou:false,
    }
  },
  // mounted(){
  //   this.anishow();
  // },
  methods: {
    // anishow :function(){
    //   $(".show").fadeIn();
    // }
  },
  created(){
    this.show = true;
    setTimeout(() => {
      this.showtwo = true;
    }, 500);
    setTimeout(() => {
      this.showtwo = true;
    }, 1000);
    setTimeout(() => {
      this.showtwo = true;
    }, 1500);
  },
  components: {
    Partner,
    Card,
    Filtertool,
    Footer,
    Menubar,
    Buttonbase
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
[v-cloak] {
      display: none;
  }

.v-enter-active,
.v-leave-active {
  transition: opacity 0.5s ease;
}

.v-enter-from,
.v-leave-to {
  opacity: 0;
}
</style>



